<template>
  <transition :name="transitionName">
    <component :key="$route.fullPath" :is="getLayout">
      <slot />
    </component>
  </transition>
</template>

<script>
import DefaultLayout from "./DefaultLayout";
import IndexLayout from "./IndexLayout";

export default {
  name: "TheLayout",
  components: {
    DefaultLayout,
    IndexLayout,
  },
  data: function () {
    return {
      layout: null,
      transitionName: null,
    };
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      let transitionName = "";
      const toDepth = to.meta.pageDepth;
      const fromDepth = from.meta.pageDepth;
      transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
      this.transitionName = transitionName || "slide-right";
      next();
    });
  },
  computed: {
    getLayout: function () {
      return this.$route.meta.layout || "DefaultLayout";
    },
    // transitionName: function () {
    //   // console.log(this.$route.meta);
    //   return this.$route.meta.transition;
    // },
  },
};
</script>
