<template>
  <main id="contents" class="l_main">
    <header class="m_header">
      <h2 class="m_header__title">DIVING(다이빙)</h2>
      <span class="m_header__text">{{
        now_date | dateFormat("yyyy-MM-dd")
      }}</span>
    </header>
    <div class="m_tbl-match">
      <table>
        <tbody>
          <tr v-for="match in match_list" :key="match.lidx">
            <td>{{ match.gameno }}</td>
            <td>
              <span :class="'m_match-type t_' + STRMAP_CDC_CODE[match.CDC]">{{
                STRMAP_CDCICON[match.CDCICON]
              }}</span
              >{{ match.CDCNM }}
            </td>
            <td class="t_title">{{ match.CDBNM }}</td>
            <td>
              <button @click="goInput(match)" class="m_btn" type="button">
                심사하기
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- S: 모달창 영역 -->
    <!-- <section class="l_modal">
               <div class="l_modal__con">

               </div>
            </section> -->
    <!-- E: 모달창 영역 -->
  </main>
</template>

<script>
// @ is an alias to /src

export default {
  name: "List",
  data: function () {
    return {
      /* -----------------------------------------------------------------------
         모달 관련 변수
         ------------------------------------------------------------------------*/

      /* -----------------------------------------------------------------------
         메인 변수
         ------------------------------------------------------------------------*/
      now_date: new Date(),
      match_list: [],
      STRMAP_CDC_CODE: STRMAP_CDC_CODE,
      STRMAP_CDCICON: STRMAP_CDCICON,
    };
  },
  created: function () {
    this.reqMatchList();
  },
  mounted: function () {
    // this.console();
  },
  methods: {
    /* -----------------------------------------------------------------------------------
            go input
         ---------------------------------------------------------------------------------- */
    goInput: function (match) {
      // const path = "/game_manager/pages/input.asp";
      // let pack = {};
      // pack.etc_obj = {
      //   seq: 11,
      // };
      // cm_fn.goto_url(path, pack);
      let pack = {};
      pack.etc_obj = {
        lidx: match.lidx,
      };
      this.$router.push({ path: "/input", query: pack });
    },
    /* -----------------------------------------------------------------------------------
            req match list
         ---------------------------------------------------------------------------------- */
    reqMatchList: function () {
      const path = "/game_manager/ajax/api/api.SWIM_300.asp";
      const params = {};
      axios.post(path, params).then(
        function (res) {
          log(res);
          if (String(res.data.result) !== "0") {
            alert("리스트 조회 실패");
            return;
          }
          this.match_list = res.data.list;
        }.bind(this)
      );
    },
  },
};
</script>
