        Number(header_info.cdc) === E_CDC_CODE_T_D ||
        Number(header_info.cdc) === E_CDC_CODE_FREE_D
      "
      class="m_tbl-input"
    >
      <div class="m_tbl-input__con">
        <table>
          <thead>
