                     <tr :class="{s_hide:hide}">
                        <td class="t_order">2</td>
                        <td>2.5</td>
                        <td class="t_name">김판국</td>
                        <td class="t_max-text">사직초</td>
                        <td>부산</td>
                        <td>
                           <button @click="openModal_input()" class="m_btn t_require" type="button">점수입력</button>
                        </td>
                     </tr>
                     <tr :class="{s_hide:hide}">
                        <td class="t_order">2</td>
                        <td>2.5</td>
                        <td class="t_name">김판국</td>
                        <td class="t_max-text">분당서현고등학교부설방송통신고등학교</td>
                        <td>부산</td>
                        <td>
                           <button @click="openModal_input()" class="m_btn t_require" type="button">점수입력</button>
                        </td>
                     </tr>
                  </tbody>
               </table>
            </div> -->
    <!--
            스프링보오드
            <div class="m_tbl-input">
               <table>
                  <thead>
                     <tr>
                        <th width="6%">순서</th>
