<script>
// @ is an alias to /src

export default {
  name: "Input",
  data: function () {
    return {
      /* -----------------------------------------------------------------------
         모달 관련 변수
         ------------------------------------------------------------------------*/
      modal_input: {
        show: false,
        sel_game: null,
        sel_idx: null,
        inputScore_info: {},
        input_text: "",
