<template>
  <div class="l_wrap">
    <header id="header" class="l_header">
      <button
        type="button"
        @click="$router.go(-1)"
        v-show="$route.meta.hideBack !== true"
        class="l_header__btn-back"
      >
        뒤로가기
      </button>
      <h1 class="l_header__title">{{ user_info.C_TNAME }}</h1>
      <span class="l_header__text t_right">{{ user_info.C_ID }}</span>
      <button
        @click="reqLogout"
        class="m_btn t_size-s t_dark-blue"
        type="button"
      >
        로그 아웃
      </button>
    </header>
    <slot />
  </div>
</template>

<script>
export default {
  name: "DefaultLayout",
  components: {},
  data: function () {
    return {
      user_info: {},
      transitionName: null,
    };
  },
  created: function () {
    this.reqProfile();
    this.$router.beforeEach((to, from, next) => {
      let transitionName = "";
      const toDepth = to.meta.pageDepth;
      const fromDepth = from.meta.pageDepth;
      transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
      this.transitionName = transitionName || "slide-right";
      next();
    });
  },
  mounted: function () {},
  methods: {
    reqProfile: function () {
      if (process.env.NODE_ENV === "development") {
        this.user_info = {
          C_ID: "다이빙_1",
          C_NM: "심판1",
          C_AUTH: "F",
          C_CDA: "E2",
          C_CDANM: "DIVING(다이빙)",
          C_POSITIONNUM: "1",
          C_TIDX: 137,
          C_TNAME: "제11회 김천 전국수영대회",
        };
        return;
      }
      const path = "/game_manager/ajax/_func/loginCheck.asp";
      const params = {};
      axios.post(path, params).then(
        function (res) {
          log(res);
          if (res.data.result !== "0") {
            alert("유저정보가 없습니다.");
            this.$router.replace({ path: "/" });
            return;
          }
          this.user_info = res.data.user_info;
        }.bind(this)
      );
    },
    /* ----------------------------------------------------------------------------------
         로그아웃 버튼
         ---------------------------------------------------------------------------------- */
    reqLogout: function () {
      if (process.env.NODE_ENV === "development") {
        document.cookie = "";
        this.$router.replace({ path: "/" });
        return;
      }
      const path = "/game_manager/ajax/api/api.SWIM_200.asp";
      const params = {};
      axios.post(path, params).then(
        function (res) {
          log(res);
          if (res.data.result !== "0") {
            alert("로그아웃 실패");
            return;
          }
          this.$router.replace({ path: "/" });
        }.bind(this)
      );
    },
  },
};
</script>
